.eltd-blog-holder {

    @media only screen and (max-width: $ipad-portrait) {
        article {
            &.format-link, &.format-quote {
                .eltd-post-text {
                    padding: 4% 3%;
                }
                .eltd-link-title,
                .eltd-post-title {
                    font-size: 17px;
                    word-break: break-all;
                }
            }

        }
    }

    @media only screen and (max-width: $laptop-landscape-medium) {
        article {
            &.format-quote {
            .eltd-post-text {
                padding: 55px 35px 40px 35px;
                }
            }

        }

    }

}
